import { 
  IRule, id, 
} from "../../../common/types/domain";
import { ISubjectView, ITeacherView } from "../../../common/types/teacherDomainView";
import { IDataBaseRule, getTeacherDomainHandelers } from "./handelers";

export const teacherDomainHandelers = getTeacherDomainHandelers();

export const readTeacherViewApiCall = async (id: id): Promise<ITeacherView> => {
  const data = await teacherDomainHandelers.teacherAPIHandeler.single(id);
  const { abonementOffers = [], subjects = [], ...rest } = data || {};
  const abonementsData = await teacherDomainHandelers.abonementOffer.list(id, { ids: abonementOffers });
  const subjectsData = await teacherDomainHandelers.subjectApiHandeler.list(id, { ids: subjects });
  const subjectRulesIds = subjectsData.flatMap(({rules}) => rules);
  const rules = await teacherDomainHandelers.rulesApiHandeler.list(id, {ids: subjectRulesIds});

  const subjectViews = subjectsData.map((subject): ISubjectView => {
    const thisSubjectRules = subject.rules.map((id) => rules.find(({id: itemId}) => itemId === id));
    const viewDataBaseRules = thisSubjectRules.filter((item) => !!item) as IDataBaseRule[];
    const viewRules: IRule[] = viewDataBaseRules.map((dataBaseRule): IRule => {
    const { timeStart, ...rest } = dataBaseRule;
      return {
        ...rest,
        timeStart: timeStart.toDate(),
      }
    });

    return {
      ...subject,
      rules: viewRules,
    }
  })

  const teacherView: ITeacherView = {
    ...rest,
    abonementOffers: abonementsData,
    subjects: subjectViews,
  }

  return teacherView;
};
